.header-top-container {
  background: rgba(10, 10, 10, 0.65);
  padding: 15px 15px 5px 15px;
}

.header-top {
  z-index: 99;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  line-height: 4rem;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.links-tab {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
  font-size: 1.5rem;
}

.links-tab.links-tab-col {
  -ms-flex-direction: column;
      flex-direction: column;
}

.links-tab a {
  color: white;
  text-decoration: none;
  padding: 0 3px;
}

.links-tab a.selected-link {
  font-weight: bold;
  font-size: 2.1rem;
}

.retro-logo {
  margin-right: 6px;
  background: url('/static/web_retrobomblogo.png');
  background-size: cover;
  max-width: 342px;
  max-height: 60px;
  width: calc(9vmin * 5.7);
  height: 9vmin;
}

.logo-text {
  color: white;
  font-size: 2rem;
}

.ue4-logo {
  cursor: pointer;
  background: url('/static/UE_Logo_Icon_White.png');
  background-size: cover;
  width: 51px;
  height: 51px;
}

.header-link {
  margin-left: 10px;
  opacity: 0.75;
  -webkit-transition: opacity .5s;
  -o-transition: opacity .5s;
  transition: opacity .5s;
}

.header-link:hover {
  opacity: 1;
}



